@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.filter{margin-right:auto;width:12rem}@media (min-width: 576px){.filter{margin-left:auto;margin-right:0}}.filter-btn,.filter .btn{padding:0.8rem 0.8rem;width:100%;text-align:start}.filter-btn:before{content:''}

@media (min-width: 768px){.country-img{height:9rem;overflow:clip}}.country-subtitle{font-weight:600;margin-right:0.5rem}.list-group-item{border:none;padding:0}

.App{height:100vh;font-family:"Nunito Sans",sans-serif;font-size:14px}.App h1{font-size:1.5rem;font-weight:800}.App h5{font-weight:600}.App nav{padding:1.25rem 0}.App main .input-group-text,.App main .input{padding:0.75rem}.App nav{margin-bottom:3.5rem}.App .dark-mode{background-color:#202c37;color:#fff}.App .dark-mode main .card,.App .dark-mode main .card-body .list-group-item,.App .dark-mode main nav,.App .dark-mode main .input-group-text,.App .dark-mode main .search-input,.App .dark-mode main .filter{background-color:#2b3945;color:#fff}.App .dark-mode main .search-input,.App .dark-mode main .input-group-text{border:1px solid #2b3945}.App .dark-mode main .search-input::-webkit-input-placeholder{color:#fff}.App .dark-mode main .search-input:-ms-input-placeholder{color:#fff}.App .dark-mode main .search-input::placeholder{color:#fff}.App .light-mode{background-color:#fafafa;color:#111517}.App .light-mode main .card,.App .light-mode main .card-body .list-group-item,.App .light-mode main nav,.App .light-mode main .input-group-text,.App .light-mode main .search-input{background-color:#fff;color:#111517}.App .light-mode main .search-input,.App .light-mode main .input-group-text{border:1px solid #fff}.App .light-mode main .search-input::-webkit-input-placeholder{color:#858585}.App .light-mode main .search-input:-ms-input-placeholder{color:#858585}.App .light-mode main .search-input::placeholder{color:#858585}

